<!-- 头部 -->
<template lang="pug">
section.head
  div.nav
    div.logo
      span.title
        i(class="el-icon-grape")
        span &nbsp;组件/工具/规范/指南 1.6.4
    div.weather
      span(v-if="temperature") {{temperature}}℃
      span(v-if="summary" style="margin-left: 10px") {{summary}}
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      user: {},
      temperature: '', // 摄氏度
      summary: '', // 天气概述
      timer: null,
    };
  },
  computed: {
  },
  methods: {
    getSummary() {
      // 天气api
      // https://docs.pirateweather.net/en/latest/API/#units
      let _this = this;
      let geo = navigator.geolocation
      if (geo) { // 判断是否可以获取当前经纬度
        geo.getCurrentPosition(function(res){
          let lat=res.coords.latitude // 纬度
          let lon=res.coords.longitude // 经度
          console.log(lat,lon)
          fetch(`https://api.pirateweather.net/forecast/Fy8RV5RVFe6mnwJN/${lat},${lon}?units=ca&exclude=minutely,daily,hourly`, {method: 'GET'}).then((resp) => {
            return resp.json();
          }).then((ret) => {
            _this.temperature = ret?.currently.temperature;
            _this.summary = ret?.currently.summary;
          });
        })
      } else {
        console.log('该浏览器不支持定位');
      }
    },
  },
  mounted() {
    this.getSummary();
    // let _this = this;
    // this.timer=setInterval(() => {_this.getSummary()},1000*60);
  },
  destroyed() {
    this.timer = null;
  }
};
</script>

